<template>

  <div id="headers">
  <div id="menu">
    <el-menu

      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#606266"
      text-color="#ffffff"
      active-text-color="#ffd04b" >
      <el-menu-item index="1"><a href="http://www.zhuxf.net">首页</a></el-menu-item>
      <el-menu-item index="2"><a href="http://www.zhuxf.net/photo">照片墙</a></el-menu-item>


      <el-menu-item index="4"><a href="http://watterson365git.github.io">简历&&项目</a></el-menu-item>
    </el-menu>

  </div>


    <div id="lunbotu1">
<!--      电脑端轮播图-->
