          <el-carousel-item v-for="item in list2" :key="item">
            <h3><img :src="item.idView" alt=""></h3>
          </el-carousel-item>
        </el-carousel>
      </template>
    </div>

  </div>

</template>

<script>
  export default {
    name: "headers",
    data(){
      return{
        list:
          [{id:0,idView:require("../assets/img/img_header/alone.png")},
          {id:1,idView:require("../assets/img/img_header/lab.png")},
          {id:2,idView:require("../assets/img/img_header/red.png")}],
        list2:
          [{id:0,idView:require("../assets/img/img_header/alone2.png")},
            {id:1,idView:require("../assets/img/img_header/lab2.png")},
            {id:2,idView:require("../assets/img/img_header/sch2.png")}]
      }
    }

  }

</script>
<style>


  #menu ul{
    padding-left: 30px;

  }

