<template>
    <div id="bodyi">

      <div class="b_left">
        <div class="b_left_2">
            <div  class="skill" @click="html()">Html</div>
             <div  class="skill" @click="css()">CSS</div>
            <div class="skill" @click="vue()">Vue</div>
           <div  class="skill" @click="js()">JS</div>
           <div  class="skill" @click="miniapp()">小程序</div>


        </div>

              <div class="b_left_1" v-for="(item,index) in blogs" >

                  <a :href='blogs_url[index]'>
                  <span>{{item}}</span>
                  <img :src="pic_url[index]" alt="">
                  <div class="pic"></div>





                  </a>
              </div>

              <div class="nextpage" @click="nextpage()"><span>下一页</span></div>
<!--        博客文章渲染到这个div里-->
      </div>
      <div class="b_right">
        <div class="b_right_1">
          <div class="face">
            <img src="../assets/img/img_body/face.jpg" alt="" style="">
          </div>
            <div class="welcome">Welcome to my Blog</div>
          <div class="social" >
              <a :href='link' v-for="(item,index) in social1" @mouseover="mouseover(index)" @mouseleave="mouseleave(index)"  >
                  <img :src="item.idView" alt=""  >
              </a>
              <div class="wechart" v-show="isshow1">
                  <img src="../assets/img/img_body/wechart_add.jpg" alt="">
              </div>

              <div class="QQ" v-show="isshow2">
                  <img src="../assets/img/img_body/QQ_add.png" alt="">
              </div>



          </div>
        </div>


        <div class="b_right_3">
            <div style="text-align: center;padding-top: 3px"><h3>随机推送</h3></div>
            <ul>
                <lable v-for="(item,index) in news"> <a :href="news_url[index]"><li >{{item}}</li></a></lable>


            </ul>
        </div>
        <div class="b_right_4"><img src="../assets/img/img_body/cba642fc15c3a6da0ef1d735a557f3ee .gif" alt=""></div>
      </div>
    </div>
</template>

<script>
    import  {request} from '../../request.js'

    export default {
      name: "bodyi",
      data(){
          return{
           social1:[
                  {id:0,idView:require("../assets/img/img_body/github.svg"),},
                  {id:1,idView:require("../assets/img/img_body/wechart.svg")},
                  {id:2,idView:require("../assets/img/img_body/QQ.svg")},
                  {id:3,idView:require("../assets/img/img_body/email.svg")},
                  {id:4,idView:require("../assets/img/img_body/weibo.svg")}],

              social2:[{id:0,idView:require("../assets/img/img_body/github2.svg")},
                  {id:1,idView:require("../assets/img/img_body/wechart2.svg")},
                  {id:2,idView:require("../assets/img/img_body/QQ2.svg")},
                  {id:3,idView:require("../assets/img/img_body/email2.svg")},
                  {id:4,idView:require("../assets/img/img_body/weibo2.svg")}],

              social3:[{id:0,idView:require("../assets/img/img_body/github.svg")},
                  {id:1,idView:require("../assets/img/img_body/wechart.svg")},
                  {id:2,idView:require("../assets/img/img_body/QQ.svg")},
                  {id:3,idView:require("../assets/img/img_body/email.svg")},
                  {id:4,idView:require("../assets/img/img_body/weibo.svg")}],
              index_now:0,
              isshow1:false,
              isshow2:false,
              all_blogs:[],
              blogs:[],
              blogs_url:[],
              huancun:[],
              count:0,
              news:[],
              news_url:[],
              pic_url:[],

           }
      },
        computed:{
          link(){
              if(this.index_now === 0){return 'https://github.com/watterson365git'}
              else if(this.index_now === 3){
                  return 'https://email.163.com/#from=ntes_product'
              }else if(this.index_now === 4){
                  return 'https://weibo.com/watterson365/home?leftnav=1'
              }else{
                  return ''
              }


          },

        },
        methods:{
            mouseover(index){
                this.index_now = index;
                this.social1.splice(index,1,this.social2[index])
                if(index === 1){
                    this.isshow1 = true;
                }else if(index === 2){
                    this.isshow2 = true;
                }else{
                    this.isshow1 = false;
                    this.isshow2 = false;
                }

            },
            mouseleave(){
                var arr = this.social3.concat();
                this.social1 = arr;
                this.isshow1 = false;
                this.isshow2 = false;
            },
            nextpage:function(){
                function group(array, subGroupLength) {
                    let index = 0;
                    let newArray = [];
                    while(index < array.length) {
                        newArray.push(array.slice(index, index += subGroupLength));
                    }
                    return newArray;
                }

                if(Math.ceil(this.all_blogs.length/10)===1){
                     return  alert('这是最后一页哦');
                }else{
                    // console.log(this.blogs);
                    // console.log( Math.ceil(this.all_blogs.length/10) );
                    this.huancun = group(this.all_blogs, 6);


                    this.blogs = [...this.blogs,...this.huancun[this.count]];
                    if(this.count===this.huancun.length){
                         return  alert('这是最后一页哦');
                    }else{
                        this.count++;
                    }

                    console.log(this.huancun);
                    console.log(this.count);

                }

            },
            vue:function() {

                request ({
                    url:'/vue/'
                })
                    .then(res=>{
                        console.log(res);


                        this.all_blogs =res.data;
                        this.blogs =res.data.map((x)=>{return x.split('.')[0]});

                        this.blogs_url=this.blogs.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});
                        console.log(this.blogs);

                    })
                    .catch(err=>{
                        console.log(err);});
            },
            html:function() {

                request ({
                    url:'/html/'
                })
                    .then(res=>{
                        console.log(res);


                        this.all_blogs =res.data;
                        this.blogs =res.data.map((x)=>{return x.split('.')[0]});

                        this.blogs_url=this.blogs.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});
                        console.log(this.blogs);

                    })
                    .catch(err=>{
                        console.log(err);});
            },
            css:function() {

                request ({
                    url:'/css/'
                })
                    .then(res=>{
                        console.log(res);


                        this.all_blogs =res.data;
                        this.blogs =res.data.map((x)=>{return x.split('.')[0]});

                        this.blogs_url=this.blogs.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});
                        console.log(this.blogs);

                    })
                    .catch(err=>{
                        console.log(err);});
            },
            miniapp:function() {

                request ({
                    url:'/miniapp/'
                })
                    .then(res=>{
                        console.log(res);


                        this.all_blogs =res.data;
                        this.blogs =res.data.map((x)=>{return x.split('.')[0]});

                        this.blogs_url=this.blogs.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});
                        console.log(this.blogs);

                    })
                    .catch(err=>{
                        console.log(err);});
            },
            js:function() {

                request ({
                    url:'/javascript/'
                })
                    .then(res=>{
                        console.log(res);


                        this.all_blogs =res.data;
                        this.blogs =res.data.map((x)=>{return x.split('.')[0]});

                        this.blogs_url=this.blogs.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});
                        console.log(this.blogs);

                    })
                    .catch(err=>{
                        console.log(err);});
            },





        },

        created() {

            request ({
                url:'/show/'
            })
                .then(res=>{
                    console.log(res);


                    this.all_blogs =res.data;
                        this.blogs =res.data.map((x)=>{return x.split('.')[0]});

                        this.blogs_url=this.blogs.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});
                        //console.log(this.blogs);

                        //news栏
                    var arr = [];

                    for(let i =0;i<6;i++){
                        arr.push(this.blogs[Math.floor(Math.random()*this.blogs.length)])

                    }
                    this.news=arr.concat();
                    this.news_url=arr.map((x)=>{return 'http://www.zhuxf.net/blog/?name='+x +'.html'});

                    //封面图片

                    this.pic_url=this.blogs.map((x)=>{return './pic/'+x +'.png'});
                    console.log(this.blogs);
                    console.log(this.pic_url);


                })
                .catch(err=>{
                    console.log(err);});

        },

    }
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Ma+Shan+Zheng&display=swap');


  #bodyi{
  display: flex;

  padding-top: 40px;
}

  .b_left{
    height: 100%;
    flex: 2;
    margin-right: 35px;
    margin-left: 10%;
  }

.b_left_1{

    font-size: 150%;
    padding-top: 10px;
  text-align: center;
  height: 220px;
  margin-bottom: 10px;
  background-color:#ffffff;
  border-radius: 1%;
}

