    color: #3c3f41;
}

 .b_left_1 .pic{
     background-color: rosybrown;
     height:160px;

 }

.nextpage{
    color: orange;
    font-size: 120%;
    text-align: center;
    height: 30px;
    margin-bottom: 10px;
    background-color: #357aa7;
    border-radius: 1%;
}

.b_left_2{
  height: 80px;
  background-color: #ffffff;
  margin-bottom: 20px;

}



.b_right{
  height: 100%;
  flex: 1;
  margin-right:11%;
}


.b_right_1{
    margin-bottom: 22px;
    background-color: #ffffff;
    border-radius: 1%;
    transition: box-shadow 300ms,transform 300ms;
  }
.b_right_1:hover{
  box-shadow: 3px 3px 5px rgba(100,100,100,0.6);
  transform: translate(0,-3px);
}


  .b_right_3{
  height: 300px;
  border-radius: 1%;
  transition: box-shadow 300ms,transform 300ms;
  background-color: #ffffff;
  }

  .b_right_3 a{
      text-align: left;
      color: orange;
  }

    .b_right_3:hover{
        box-shadow: 3px 3px 3px rgba(100,100,100,0.6);
        transform: translate(0,-3px);
    }

    .b_right_4 img{
        height: 300px;
        display: block;
        margin: auto;

    }


.skill{
  display: inline-block;
  float: left;
  margin-left: 1%;
  margin-top: 25px;
  height: 30px;
  width: 80px;
  text-align: center;
  line-height: 30px;
  border-radius: 50px;
