  display: none;
}

  .b_left{
    margin-top: -50px;
    height: 100%;

    flex: 1;
    margin-right: 5%;
    margin-left: 5%;
  }

  .b_left_2{
    display: none;

  }

    .b_left_1{
        color: #545c64;
        font-size: 110%;
        padding-top: 10px;
        margin-top: 18px;
        text-align: center;
        height: 180px;
        margin-bottom: 10px;
        background-color:#ffffff;
        border-radius: 1%;
    }

    .b_left_1 span{
        color: #3c3f41;
    }

    .b_left_1 .pic{
        background-color: rosybrown;
